"use client";

import Image from "next/image";
import { motion } from "framer-motion";

export default function Header() {
  return (
    <header className="bg-white border-b border-gray-300 shadow-md py-2">
      {/* 4 Column Grid */}
      <div className="max-w-7xl mx-auto px-6 grid grid-cols-4 items-center">

        {/* ============================
            COL 1 — BLF LOGO
        ============================ */}
        <div className="flex items-center">
          <Image
            src="/assets/img/logos/header1/BEAUTIFUL LIFE LOGO 2.jpg"
            alt="Beautiful Life Logo"
            width={70}
            height={70}
            className="rounded-full object-cover"
          />
        </div>

        {/* ============================
           {/* COL 2 – Title + Butterfly (shift fully to left) */}
<div className="flex items-center justify-start ml-[-40px] relative">
    <h1 className="text-4xl font-bold text-gray-900 whitespace-nowrap">
        Beautiful Life
    </h1>

        {/* BUTTERFLY – sits on LIFE tail and flies RIGHT → LEFT toward the title */}
<motion.img
  src="/assets/img/logos/header1/butterfly (2).gif"
  alt="Butterfly"
  style={{
    width: "75px",
    height: "75px",
    transform: "scaleX(-1)", // face LEFT → fly toward title
  }}
  className="absolute top-[4px] right-[-20px]" // sits exactly on LIFE tail
  animate={{
    x: [0, -12, 0], // flutter RIGHT → LEFT
    y: [0, -6, 0],
  }}
  transition={{
    duration: 4,
    repeat: Infinity,
    ease: "easeInOut",
  }}
/>
        </div>

        {/* ============================
            COL 3 — Register / Login
        ============================ */}
        <nav className="flex items-center justify-center gap-6 text-[16px]">
          <a href="#" className="text-blue-600 hover:underline font-medium">
            Register
          </a>
          <a href="#" className="text-blue-600 hover:underline font-medium">
            Login
          </a>
          <a href="#" className="text-blue-600 hover:underline font-medium whitespace-nowrap">
            Forgot Password
          </a>
        </nav>

        {/* ============================
            COL 4 — FOF + Panther + A2Z
        ============================ */}
        <div className="flex items-center justify-end gap-4">

          <span className="text-gray-700 font-medium whitespace-nowrap">
            Friends of Friends (FOF)
          </span>

          {/* Panther */}
          <Image
            src="/assets/img/logos/header1/foflogo.jpg"
            alt="FOF Panther Logo"
            width={55}
            height={55}
            className="object-contain"
          />

          {/* A2Z Logo */}
          <Image
            src="/assets/img/logos/header1/a2zgroups.jpg"
            alt="A2Z Group Logo"
            width={55}
            height={55}
            className="object-contain"
          />
        </div>

      </div>
    </header>
  );
}
